@import '../Variables.scss';

header
{
    background-color: $primaryMainColor;
    font-family: $primaryFontFamily;
}

.logotype
{
    display: flex;
    text-decoration: none;
    color: $secondarySubColor;
    font-size: 0;

    p
    {
        font-size: $mediumSize;
        margin: 0;
        
        &::first-letter
        {
            color: $secondaryMainColor;
            font-size: $largeSize;
        }
    }

    &:hover
    {
        color: $secondarySubColor;
        text-decoration: none;
    }
}

.navList
{
    display: table;
    list-style: none;
    text-align: center;
    margin: 0;
    padding: 0;

    li
    {
        display: table-cell;
        position: relative;

        a
        {
            display: inline-block;
            position: relative;
            text-decoration: none;
            color: $secondarySubColor;
            font-size: calc(#{$smallSize} * 1.25);
            padding: calc(#{$smallSize} * 1.375);
        }

        &::after
        {
            display: block;
            position: absolute;
            background: $secondarySubColor none repeat scroll 0 0;
            height: 0.25rem;
            left: 50%;
            bottom: 0;
            width: 0;
            transition: width 250ms ease-in-out, left 250ms ease-in-out;
            content: '';
        }

        &:hover:after
        {
            width: 100%;
            left: 0;
        }
    }
}